<template>
    <div class="ContainerView">
        <div class="ContainerView__data">
          <template v-for="data in container">
              <DataField :data="data" :key="data.index" @updateField="updateField(data.index, $event)"/>
          </template>
        </div>
        <div class="ContainerView__actions">
            <Button red @onClick="cancel">ATCELT</Button>
            <Button :disabled="!isChanges" @onClick="updateContainer">SAGLABĀT</Button>
        </div>
    </div>
</template>

<script>
import Button from '../components/Button.vue';
import DataField from '../components/DataField.vue';

export default {
    name: 'ContainerView',

    components: {
        Button, DataField
    },

    data() {
        return {
            container: this.$store.getters.container
        }
    },

    computed: {
        isChanges() {
            return JSON.stringify(this.$store.getters.container) !== JSON.stringify(this.container);
        }
    },

    methods: {
        cancel() {
            this.$router.go(-1);
        },
        updateField(index, newData) {
            this.container = this.container.map(item => {
                if (item.index === index) {
                    return { ...item, value: newData }
                }

                return { ...item }
            });

        },
        updateContainer() {
            this.$store.dispatch('updateContainer', this.container);
            this.$router.push('project');
        }
    }
}
</script>

<style lang="scss" scoped>
.ContainerView {
    display: flex;
    flex-direction: column;
    height: 100%;

    &__data {
        flex: 1;
    }

    &__actions {
        border: 1px solid grey;
        display: flex;
        height: auto;
        justify-content: space-around;
        padding: 10px 0;
    }
}
</style>